.category-block {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  padding: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.category-title {
  color: #333;
  margin: 10px 0;
  font-size: 1.8em;
}

.book-row {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.book-card {
  width: 200px;
  height: 380px;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.book-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.book-image {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.book-info {
  text-align: center;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  display: flex;
}

.book-info h3 {
  color: #333;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 10px 0;
  font-size: 1.2em;
  overflow: hidden;
}

.book-info p {
  color: #777;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0;
  font-size: 1em;
  overflow: hidden;
}

.hidden-book {
  display: none;
}

.see-more-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 5px 10px;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
}

.see-more-btn:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

